import map from 'lodash-es/map.js'
import keys from 'lodash-es/keys.js'
import genID from 'wsemi/src/genID.mjs'
import dtmapping from 'wsemi/src/dtmapping.mjs'
import dtpick from 'wsemi/src/dtpick.mjs'
import nowms2str from 'wsemi/src/nowms2str.mjs'
import now2strp from 'wsemi/src/now2strp.mjs'


let keyTable = 'ruleGroups'
let tableNameCht = ''
let tableNameEng = 'Rule groups'


let settings = {
    id: {
        pk: true,
        name: '',
        type: 'STRING',
    },
    order: {
        name: '',
        type: 'INTEGER',
    },
    name: {
        name: '',
        type: 'STRING',
    },
    description: {
        name: '',
        type: 'TEXT',
    },
    from: {
        name: '',
        type: 'STRING',
    },
    crules: {
        name: '', //json, { '___all___': {show:'y',active:'n'}, 'A/A': {show:'y',active:'y'}, 'A/B': {show:'n',active:'n'} }
        type: 'TEXT',
    },
    userId: {
        name: '',
        type: 'STRING',
    },
    timeCreate: {
        name: '',
        type: 'STRING',
    },
    userIdUpdate: {
        name: '',
        type: 'STRING',
    },
    timeUpdate: {
        name: '',
        type: 'STRING',
    },
}

let funNew = (ndata = {}) => {
    let o = dtmapping(ndata, keys(settings))
    o.id = `${now2strp()}-${genID()}`
    o.userIdUpdate = o.userId
    o.timeCreate = nowms2str()
    o.timeUpdate = o.timeCreate
    // o.isActive = 'y'
    return o
}

let funTest = () => {
    let rs = [
        ['1', `{ "___all___": { "show": "y", "active": "n" }, "A/A": { "show": "y", "active": "y" }, "A/B": { "show": "n", "active": "n" } }`],
        ['2', `{ "___all___": { "show": "y", "active": "n" } }`],
        ['3', `{ "___all___": { "show": "y", "active": "y" } }`],
    ]
    rs = map(rs, ([g, crules], k) => {
        let v = funNew({ userId: 'id-for-admin', crules })
        v.id = `id-for-${g}`
        v.order = k
        v.name = g
        v.description = `${g}`
        v = dtpick(v, keys(settings))
        return v
    })
    console.log(`: ${keyTable} `, rs)
    return rs
}

let tab = {
    keyTable,
    tableNameCht,
    tableNameEng,
    settings,
    funNew,
    funTest,
}


export default tab

