import map from 'lodash-es/map.js'
import keys from 'lodash-es/keys.js'
import genID from 'wsemi/src/genID.mjs'
import dtmapping from 'wsemi/src/dtmapping.mjs'
import dtpick from 'wsemi/src/dtpick.mjs'
// import nowms2str from 'wsemi/src/nowms2str.mjs'
import now2strp from 'wsemi/src/now2strp.mjs'


let keyTable = 'targets'
let tableNameCht = ''
let tableNameEng = 'Targets'


let settings = {
    id: {
        pk: true,
        name: '', //, ., unicode
        type: 'STRING',
    },
    order: {
        name: '',
        type: 'INTEGER',
    },
    description: {
        name: '',
        type: 'TEXT',
    },
    from: {
        name: '',
        type: 'STRING',
    },
}

let funNew = (ndata = {}) => {
    let o = dtmapping(ndata, keys(settings))
    o.id = `${now2strp()}-${genID()}`
    // o.timeCreate = nowms2str()
    // o.timeUpdate = o.timeCreate
    // o.isActive = 'y'
    return o
}

let funTest = () => {
    let rs = [
        'A',
        'A/A',
        'A/A/A',
        'A/A/A/',
        'A/A/A/',
        'A/B',
        'A/B/A',
        'A/B/A/',
        'A/B/A/',
        'A/B/B',
        'A/B/B/',
        'A/B/B/',
        'B',
        'B/A',
        'B/A/A',
        'B/A/A/',
        'B/A/A/',
        'B/B/A',
        'B/B/B',
        'B/B/B/',
        'B/B/B/',
    ]
    rs = map(rs, (key, k) => {
        let v = funNew({ description: key })
        v.id = key
        v.order = k
        v = dtpick(v, keys(settings))
        return v
    })
    console.log(`: ${keyTable} `, rs)
    return rs
}

let tab = {
    keyTable,
    tableNameCht,
    tableNameEng,
    settings,
    funNew,
    funTest,
}


export default tab

